import React, { useState, useCallback, useMemo } from 'react';
import { Typography, IconButton, Menu, MenuItem, ListItemIcon, CircularProgress, } from '@material-ui/core';
import { css } from '@emotion/core';
import { Spacer } from 'common/styles';
import TextField from 'common/inputs/formik/TextField';
import { PlayArrow, Stop, FilterList, MoreVert, ClearAll } from '@material-ui/icons';
import { ReadyState } from 'core/api';
import { useDebounceFormikSubmit } from 'utils/hooks';
export const wrapper = (theme) => css `
  display: block;
  ${theme.breakpoints.up('sm')} {
    display: flex;
    align-items: baseline;
  }
`;
export const filterWrapper = css `
  display: flex;
  align-items: center;
`;
export const filterField = css `
  margin-left: 1rem;
  margin-right: 1rem;
`;
const Header = ({ readyState, connect, disconnect, clear }) => {
    const [anchorEl, setAnchorEl] = useState();
    const helperText = 'Supports operators and, or, (), and "str"';
    const stateMap = useMemo(() => ({
        [ReadyState.Open]: {
            onClick: disconnect,
            Icon: Stop,
            label: 'Stop',
            heading: 'Streaming',
        },
        [ReadyState.Connecting]: {
            label: 'Start',
            Icon: CircularProgress,
            heading: 'Connecting',
            disabled: true,
        },
        [ReadyState.Closed]: {
            onClick: connect,
            Icon: PlayArrow,
            label: 'Start',
            heading: 'Disconnected',
        },
    }), [connect, disconnect]);
    const { onClick, Icon, heading, label, disabled = false } = stateMap[readyState];
    const handleMenuClose = useCallback(() => setAnchorEl(undefined), []);
    const handleMenuOpen = useCallback((event) => setAnchorEl(event.currentTarget), []);
    useDebounceFormikSubmit();
    return (<div css={wrapper}>
      <div>
        <Typography variant="h6">Server Log</Typography>
        <Typography variant="subtitle1" color="textSecondary">
          {heading}
        </Typography>
      </div>
      <Spacer />
      <div css={filterWrapper}>
        <FilterList />
        <TextField css={filterField} id="query" name="query" label="Filter" helperText={helperText}/>
        <IconButton onClick={handleMenuOpen}>
          <MoreVert />
        </IconButton>
      </div>
      <Menu id="log-menu" anchorEl={anchorEl} open={!!anchorEl} onClose={handleMenuClose}>
        <MenuItem>
          <TextField id="lines" name="lines" label="Max Lines" type="number"/>
        </MenuItem>
        <MenuItem onClick={clear}>
          <ListItemIcon>
            <ClearAll />
          </ListItemIcon>
          Clear
        </MenuItem>
        <MenuItem onClick={onClick} disabled={disabled}>
          <ListItemIcon>
            <Icon />
          </ListItemIcon>
          {label}
        </MenuItem>
      </Menu>
    </div>);
};
export default Header;
